.App {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background: linear-gradient(hsl(273, 75%, 66%), hsl(240, 73%, 65%));
}
* {
    font-family: 'Kumbh Sans', sans-serif;
}

.faqs {
    padding: 50px;
    padding-bottom: 20px;
}
.faqs > h2 {
    color: hsl(237, 12%, 33%);
    font-size: 28px;
    padding-bottom: 20px;
    font-weight: bold;
}

.faq-answer {
    padding: 3px 0;
    color: hsl(240, 6%, 50%);
    font-size: 14px;
    opacity: 0;
    max-height: 0;
    overflow-y: hidden;
    transition: all 0.6s ease;
}

.divider {
    height: 1px;
    background-color: hsl(240, 5%, 91%);
}

.faq-open .faq-question {
    font-weight: bold;
    color: hsl(238, 29%, 16%);
}


.faq-question {
    display: block;
    cursor: pointer;
    width: 100%;
    text-align: left;
    padding: 15px 15px 15px 0;
    color: hsl(237, 12%, 33%);
    transition: all 0.6s ease;
    position: relative;
}
.faq-question:hover {
    color: hsl(14, 88%, 65%);
}

.faq-question > span {
    float: right;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    transition: all 0.8s ease;
}

.faq-open .faq-question span {
    -webkit-transform: translateY(-50%) rotate(180deg);
            transform: translateY(-50%) rotate(180deg);
}

.faqs .faq-open .faq-answer {
    opacity: 1;
    max-height: 64px;
    margin-bottom: 10px;
}


.card {
    width: 930px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background-color: white;
    border-radius: 20px;
    margin: 25px;
}


.image-container {
    position: relative;
    padding: 50px 0 50px 0;
    z-index: 0;
}

.sec-img {
    position: absolute;
    top: -355px;
    left: -580px;
    z-index: -1;
}

.outer {
    position: absolute;
    top: 190px;
    left: -90px;
}

.inner picture {
    margin-left: -80px;
}

.inner {
    overflow: hidden;
}
@media (max-width: 890px) {
    .card {
        max-width: 450px;
        grid-template-columns: repeat(1, 1fr);
        margin-top: 120px;
    }
    .image-container {
        padding: 0;
        margin: 0 auto;
        margin-top: -110px;
    }
    .inner {
        overflow: visible;
    }
    .outer {
        display: none;
    }
    .sec-img {
        top: 100px;
        left: -50px;
    }
    .faqs h2 {
        text-align: center;
    }
}
